<script>
// Component
import { translate } from '@/plugins/language.js';
import {useTitle} 	from 'vue-page-title';
import {useRoute} 	from 'vue-router'
import { loadAndStoreConfigData } from '@/plugins/config';
import storageManager from '@/plugins/storage';
import CryptoJS from 'crypto-js';
import requestClient from '@/plugins/requestClient';
const Request = new requestClient();

import debug from '@/plugins/Logger.js';


export default {
  name: 'Login',
  created() {
    debug.option(this.$route);
  },
  data() {
    return {
      hostkey:this.$Key,
      logo:require('@/assets/images/color-logo.png'),
      email: '',
      password: '',
      processing_text: translate('database-connect'),
      showPassword: false,
      returnSecureToken:true,
      passwordFieldType: "password",
      activeBlock: false,
      isLoaded: false,
      siteName:'',
      loginLogo:'',
      loginBg:'',
      configs: storageManager.get('configs'),
      errors: [],
      isLoading: false,
      showForm: false,
    }
  },
  components: {},
  methods: {
    translate,
    async login() {
        try {
          this.isLoading = true;
          if(this.configs.siteType==="origin") {// ---------------------------- ORIGIN ACCESS
            console.log("origin");
            this.errors = [];
            this.activeBlock   = true;
            const requestData = {
              method: 'find',
              args: [
                {
                  $and: [
                    { role: 'root' },
                    { username: this.email }
                  ]
                }
              ]
            };
            const resAPILogin = await Request.POST('user/query', requestData, this.configs.key);
            const getLogin    = resAPILogin.data;
            const loginData   = getLogin.length > 0 ? getLogin[0] : null;

            if (loginData === null) {
              this.errors.push("ไม่พบข้อมูลผู้ใช้งาน/รหัสผ่าน");
              this.activeBlock  = false;
              this.isLoading = false;
              return;
            }
            else
            {
              const salt        = loginData.salt;
              const inputHash   = CryptoJS.SHA256(this.password + salt).toString();
              const storedHash  = loginData.password;

              console.log(inputHash , storedHash);

              if (inputHash === storedHash) {
                debug.log("Login successful");

                const session = {
                  active: true,
                  token: loginData._id,
                  refresh: "",
                  login: true,
                  userID: loginData._id,
                  user: loginData,
                  loader: false,
                  role: loginData.role,
                  nav: "superadmin-nav",
                  layout: "backend-layout",
                  current: this.configs,
                };
                storageManager.update('session',session)

                setTimeout(() => {
                  window.location.href = "/"
                }, 1000);
              } else {
                this.errors.push("ข้อมูลผู้ใช้งาน/รหัสผ่านไม่ถูกต้อง");
              }
            }
            this.activeBlock  = false;
            this.isLoading = false;
          }
          else if(this.configs.siteType==="collection") {// ---------------------------- COLLECTION ACCESS
            console.log("collection");
            this.activeBlock  = true;
            const requestData = {
              method: 'find',
              args: [
                {
                  $and: [
                    { role: 'superadmin' },
                    { username: this.email }
                  ]
                }
              ]
            };
            const resAPILogin = await Request.POST('user/query', requestData, this.configs.key);
            const getLogin    = resAPILogin.data;
            const loginData = getLogin.length > 0 ? getLogin[0] : null;

            if (loginData === null) {
              debug.log("Invalid username or password");
              this.errors.push("Invalid username or password");
              this.activeBlock  = false;
              this.isLoading = false;
              return;
            } else {
              const salt        = loginData.salt;
              const inputHash   = CryptoJS.SHA256(this.password + salt).toString();
              const storedHash  = loginData.password;

              console.log(inputHash , storedHash);

              if (inputHash === storedHash) {
                console.log("Login successful");
                const session = {
                  active: true,
                  token: loginData._id,
                  refresh: "",
                  login: true,
                  userID: loginData._id,
                  user: loginData,
                  loader: false,
                  role: loginData.role,
                  nav: "admin-nav",
                  layout: "backend-layout",
                  current: this.configs,
                };
                storageManager.update('session',session)
                setTimeout(() => {
                  window.location.href = "/"
                }, 1000);
              } else {
                debug.log("Invalid username or password");
                this.errors.push("Invalid username or password");
              }
            }
            this.activeBlock  = false;
            this.isLoading = false;
          }
          else if(this.configs.siteType==="asset") {// ---------------------------- ASSET ACCESS
            console.log("asset");
            this.activeBlock   = true;
            const requestData = {
              method: 'find',
              args: [
                {
                  $and: [
                    { username: this.email }
                  ]
                }
              ]
            };
            const resAPILogin = await Request.POST('user/query', requestData, this.configs.key);
            const getLogin    = resAPILogin.data;
            const loginData = getLogin.length > 0 ? getLogin[0] : null;
            console.log("loginData",loginData);

            if (loginData === null) {
              debug.log("Invalid username or password A");
              this.errors.push("Invalid username or password A");
              this.activeBlock  = false;
              this.isLoading = false;
              return;
            }
            else
            {
              const salt        = loginData.salt;
              const inputHash   = CryptoJS.SHA256(this.password + salt).toString();
              const storedHash  = loginData.password;

              console.log("inputHash",inputHash);
              console.log("storedHash",storedHash);

              if (inputHash === storedHash) {

                let unitList = [];
                let currentAccess = "";

                if(loginData.role==="superadmin")
                {
                  debug.log("Asset",loginData.asset);
                  const canAccess = loginData.asset.includes(this.configs.siteID);
                  debug.log("canAccess",canAccess);
                  currentAccess = this.configs;
                  
                }
                else if(loginData.role==="admin" || loginData.role==="manager")
                {
                  debug.log("Unit",loginData.unit);
                  const canAccess = loginData.parent === this.configs.siteID;
                  debug.log("canAccess",canAccess);
                  debug.log("ids", loginData.unit);
                  const resAPI = await fetch("https://gateway.cloudrestfulapi.com/api/hostname/query", {
                    method: 'POST',
                    headers: {'Content-Type': 'application/json','client-token-key':this.hostkey},
                    body: JSON.stringify({
                      method: 'find',
                      args: [
                        {
                          "_id": {
                            "$in": loginData.unit
                          }
                        }
                      ]
                    })
                  });
                  const RestReturn   = await resAPI.json();
                  unitList = RestReturn;
                  currentAccess = "none";
                  debug.log("List Return",RestReturn);
                }
                const session = {
                  active: true,
                  token: loginData._id,
                  refresh: "",
                  login: true,
                  userID: loginData._id,
                  user: loginData,
                  loader: false,
                  role: loginData.role,
                  nav: "admin-nav",
                  layout: "backend-layout",
                  current: currentAccess,
                  list: unitList,
                };
                storageManager.update('session',session)
                debug.log("session",session);
                this.$router.push("/");
                setTimeout(() => {
                  window.location.href = "/"
                }, 1000);

              } else {
                debug.log("Invalid username or password");
                this.errors.push("Invalid username or password");
              }
            }
            this.activeBlock  = false;
            this.isLoading = false;
          }
          else
          {
            this.activeBlock   = true;
            const resAPILogin  = await fetch("https://identitytoolkit.googleapis.com/v1/accounts:signInWithPassword?key=AIzaSyDODKwE1BfZOj9ieB6pisJm-p2MmudWLSw", {
              method: 'POST',
              headers: {
                'Content-Type': 'application/json'
              },
              body: JSON.stringify({
                email: this.email,
                password: this.password,
                returnSecureToken: this.returnSecureToken,
              })
            });

            const getLogin  = await resAPILogin.json();
            const status = resAPILogin.status;

            this.processing_text = "เข้าสู่ระบบสำเร็จ.....";

            if(status=="200") {
            
              // --------------------------------------------
              const session = {
                active: getLogin.registered,
                token: getLogin.idToken,
                refresh: getLogin.refreshToken,
                login: true,
                current: "none",
              };
              storageManager.update('session',session)
              // --------------------------------------------

              if(getLogin.registered) {
                const resAPIRole = await fetch("https://asia-southeast2-elearning-6871c.cloudfunctions.net/auth/role/", {
                  method: 'GET',
                  headers: {
                    'Content-Type': 'application/json',
                    'Authorization': 'Bearer ' + getLogin.idToken,
                  }
                });
                const getRole  = await resAPIRole.json();

                if(getRole.success) {

                  const resAPIProfile = await fetch("https://asia-southeast2-elearning-6871c.cloudfunctions.net/users/" + getRole.data.userId, {
                    method: 'GET',
                    headers: {'Content-Type': 'application/json','Authorization': 'Bearer ' + storageManager.get('session','token')},
                  });
                  const finalRes     = await resAPIProfile.json();
                  
                  let session = [];
                  if(getRole.data.role === "superadmin"){
                    const config = storageManager.get('configs');
                    session = 
                    {
                      userID: getRole.data.userId,
                      user: finalRes.data,
                      loader: false,
                      role: getRole.data.role,
                      nav: getRole.data.role + "-nav",
                      layout: "backend-layout",
                      current: config,
                    };
                  } else {
                    session = 
                    {
                      userID: getRole.data.userId,
                      user: finalRes.data,
                      loader: false,
                      role: getRole.data.role,
                      nav: getRole.data.role + "-nav",
                      layout: "backend-layout",
                    };
                  }
                  storageManager.update('session',session)

                  if(getRole.data.role==="admin" || getRole.data.role==="manager") {
                    const loadMaster = await this.loadMaster();
                    if(loadMaster) {
                      this.$swal({
                          toast: true,
                          position: 'bottom-end',
                          showConfirmButton: false,
                          timer: 500,
                          icon: 'success',
                          title: 'แจ้งเตือน',
                          text: 'เข้าสู่ระบบเรียบร้อยแล้ว',
                      }).then(() => {
                        window.location.href = "/"
                      });
                    }
                  } else {
                    setTimeout(() => {
                      window.location.href = "/"
                    }, 1000);
                  }
                }
              }
              else {
                this.$router.replace({path: '/member/unactive'});
              }
            } else {
              this.$swal({
              title: 'เกิดข้อผิดพลาด',
              text: getLogin.message,
              icon: 'error',
              confirmButtonText: 'ตกลง'
              }).then(() => {
                this.activeBlock = false;
                this.isLoading = false;
              })
            }
          }
          
        } catch (error) {
          debug.log(error)
          this.isLoading = false;
        }
    },
    async loadMaster () {
      debug.log("loadMaster");
      this.processing_text = "กำลังโหลดข้อมูลหมวดหมู่.....";
      const Category    = await this.getCategoryData();
      if(Category.success) {
        // --------------------------------------------
        const session = {
          category: Category.data,
        };
        storageManager.update('session',session)
        //return true
        this.processing_text = "กำลังโหลดข้อมูลโรงเรียน.....";
        const SessionList  = await this.getuserSession();
        if(SessionList.success) {
          // --------------------------------------------
          const session = {
            list: SessionList.data,
          };
          storageManager.update('session',session)
          // --------------------------------------------
          return true
        }
      }
    },
    async getCategoryData() {
      if(this.login) {
        try {
          const session 	 = storageManager.get('session');
          let accessToken  = session.token
          this.loading_sources = false;
          const resAPI = await fetch("https://asia-southeast2-elearning-6871c.cloudfunctions.net/catagories/", {
            method: 'GET',
            headers: {'Content-Type': 'application/json','Authorization': 'Bearer ' + accessToken},
          });
          const RestReturn   = await resAPI.json();
          debug.log("Category",RestReturn);
          return RestReturn
        } catch (error) {
          debug.log(error)
        }
      }
    },
    async getuserSession() {
      try {
          this.schoolCurrent = "none";
          const session 		= storageManager.get('session');
          if(typeof session.list !== 'undefined' && session.list.length > 0) {
            this.SessionList      = session.list;
            this.loading_sources  = true;
          } else {
            let accessToken       = session.token;
            this.loading_sources  = false;
            const resAPI          = await fetch("https://asia-southeast2-elearning-6871c.cloudfunctions.net/schools/getAdminSchool/" , {
              method: 'GET',
              headers: {'Content-Type': 'application/json','Authorization': 'Bearer ' + accessToken},
            });
            try {
              const RestReturn   = await resAPI.json();
              return RestReturn
            } catch (error) {
              debug.log("Error");
            }
          }
        } 
        catch (error) {
            debug.log(error)
        }
    },
    switchVisibility() {
      this.passwordFieldType = this.passwordFieldType === "password" ? "text" : "password";
      this.show = !this.show;
      this.$refs.btnToggle.innerText = this.show? translate('login-hide') : translate('login-show');
    },
    clearErrors() {
      this.errors = [];
    }
  },
  setup() {
      const route     = useRoute();
      const routeName = route.meta.title;
      useTitle(routeName);
  },
  async mounted() {
    const route = useRoute();
    const routeName = route.meta.title;
    useTitle(routeName);
    try {
      const data = await loadAndStoreConfigData(true);
      this.siteName  = data.siteName;
      this.loginLogo = data.loginLogo;
      this.loginBg   = data.loginBg;
      this.isLoaded  = true;
      
      // Show form with animation after loading
      setTimeout(() => {
        this.showForm = true;
      }, 300);
    } catch (error) {
      console.error(error);
    }
  },
}
</script>

<template>
  <div class="min-h-screen bg-gradient-to-br from-indigo-50 via-white to-cyan-50 flex">
    <!-- Loading Overlay -->
    <div v-if="activeBlock" class="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 backdrop-blur-sm">
      <div class="bg-white rounded-2xl p-8 max-w-sm mx-4 text-center shadow-2xl transform transition-all duration-300 scale-100">
        <div class="relative">
          <div class="w-16 h-16 border-4 border-indigo-600 border-t-transparent rounded-full animate-spin mx-auto mb-4"></div>
          <div class="absolute inset-0 w-16 h-16 border-4 border-cyan-400 border-t-transparent rounded-full animate-spin mx-auto opacity-30 animate-pulse"></div>
        </div>
        <p class="text-gray-700 font-medium animate-pulse">{{ processing_text }}</p>
      </div>
    </div>

    <!-- Left Side - Login Form -->
    <div class="flex-1 flex flex-col justify-center py-12 px-4 sm:px-6 lg:px-20 xl:px-24 relative">
      <!-- Background Pattern -->
      <div class="absolute inset-0 opacity-5">
        <div class="absolute inset-0 bg-gradient-to-r from-indigo-100 to-cyan-100"></div>
        <svg class="absolute inset-0 w-full h-full" xmlns="http://www.w3.org/2000/svg">
          <defs>
            <pattern id="grid" width="40" height="40" patternUnits="userSpaceOnUse">
              <path d="m 40 0 l 0 40 m -40 0 l 40 0" fill="none" stroke="currentColor" stroke-width="1"/>
            </pattern>
          </defs>
          <rect width="100%" height="100%" fill="url(#grid)" />
        </svg>
      </div>

      <div class="mx-auto w-full max-w-md lg:w-96 relative z-10">
        <!-- Logo and Title Section -->
        <div class="text-center" :class="showForm ? 'animate-fade-in-up' : 'opacity-0'">
          <div class="relative group">
            <img 
              v-if="isLoaded" 
              class="mx-auto h-28 w-auto transform transition-all duration-500 hover:scale-105 drop-shadow-lg" 
              :src="loginLogo" 
              :alt="siteName"
            >
            <div class="absolute inset-0 bg-gradient-to-r from-indigo-400 to-cyan-400 rounded-full blur-xl opacity-20 group-hover:opacity-30 transition-opacity duration-300"></div>
          </div>
          <h1 class="mt-6 text-4xl font-bold bg-gradient-to-r from-indigo-600 to-cyan-600 bg-clip-text text-transparent">
            {{ siteName }}
          </h1>
          <p class="mt-2 text-gray-600 font-medium">ยินดีต้อนรับเข้าสู่ระบบ</p>
        </div>

        <!-- Login Form -->
        <div class="mt-10" :class="showForm ? 'animate-fade-in-up animation-delay-200' : 'opacity-0'">
          <div class="bg-white/80 backdrop-blur-sm rounded-3xl shadow-2xl border border-white/20 p-8 hover:shadow-3xl transition-all duration-300">
            <form class="space-y-6" @submit.prevent="login">
              <!-- Email Input -->
              <div class="transform transition-all duration-300 hover:translate-y-[-2px]">
                <label for="email" class="block text-sm font-semibold text-gray-700 mb-2">
                  {{ translate('login-user') }}
                </label>
                <div class="relative group">
                  <input 
                    v-model="email" 
                    @focus="clearErrors"
                    id="email" 
                    name="email" 
                    type="text" 
                    autocomplete="email" 
                    :placeholder="translate('login-user')" 
                    required 
                    class="block w-full pl-12 pr-4 py-4 border-2 border-gray-200 rounded-xl bg-gray-50/50 placeholder-gray-400 transition-all duration-300 focus:border-indigo-500 focus:bg-white focus:ring-4 focus:ring-indigo-500/20 focus:outline-none hover:border-gray-300"
                  >
                  <div class="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none opacity-40 group-focus-within:opacity-60 transition-opacity">
                    <svg class="h-5 w-5 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z"></path>
                    </svg>
                  </div>
                </div>
              </div>

              <!-- Password Input -->
              <div class="transform transition-all duration-300 hover:translate-y-[-2px]">
                <label for="password" class="block text-sm font-semibold text-gray-700 mb-2">
                  {{ translate('login-password') }}
                </label>
                <div class="relative group">
                  <input 
                    v-model="password" 
                    @focus="clearErrors"
                    id="password" 
                    name="password" 
                    :placeholder="translate('login-password')" 
                    :type="passwordFieldType" 
                    autocomplete="off" 
                    required
                    class="block w-full pl-12 pr-16 py-4 border-2 border-gray-200 rounded-xl bg-gray-50/50 placeholder-gray-400 transition-all duration-300 focus:border-indigo-500 focus:bg-white focus:ring-4 focus:ring-indigo-500/20 focus:outline-none hover:border-gray-300"
                  >
                  <div class="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none opacity-40 group-focus-within:opacity-60 transition-opacity">
                    <svg class="h-5 w-5 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M12 15v2m-6 4h12a2 2 0 002-2v-6a2 2 0 00-2-2H6a2 2 0 00-2 2v6a2 2 0 002 2zm10-10V7a4 4 0 00-8 0v4h8z"></path>
                    </svg>
                  </div>
                  <button 
                    type="button"
                    @click="switchVisibility" 
                    ref="btnToggle"
                    class="absolute inset-y-0 right-0 pr-4 flex items-center text-sm font-medium text-indigo-600 hover:text-indigo-800 transition-colors duration-200 focus:outline-none"
                  >
                    <svg v-if="passwordFieldType === 'password'" class="h-5 w-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M15 12a3 3 0 11-6 0 3 3 0 016 0z"></path>
                      <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z"></path>
                    </svg>
                    <svg v-else class="h-5 w-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M13.875 18.825A10.05 10.05 0 0112 19c-4.478 0-8.268-2.943-9.543-7a9.97 9.97 0 011.563-3.029m5.858.908a3 3 0 114.243 4.243M9.878 9.878l4.242 4.242M9.878 9.878L3 3m6.878 6.878L21 21"></path>
                    </svg>
                  </button>
                </div>
              </div>

              <!-- Remember & Forgot -->
              <div class="flex items-center justify-between">
                <div class="flex items-center group">
                  <input 
                    id="remember-me" 
                    name="remember-me" 
                    type="checkbox" 
                    class="h-4 w-4 rounded border-gray-300 text-indigo-600 focus:ring-indigo-500 transition-colors duration-200"
                  >
                  <label for="remember-me" class="ml-3 block text-sm text-gray-700 group-hover:text-gray-900 transition-colors cursor-pointer">
                    {{ translate('login-remember') }}
                  </label>
                </div>

                <div class="text-sm">
                  <a 
                    href="forgot" 
                    class="font-medium text-indigo-600 hover:text-indigo-800 transition-colors duration-200 relative group"
                  >
                    {{ translate('login-forgot') }}?
                    <span class="absolute bottom-0 left-0 w-0 h-0.5 bg-indigo-600 group-hover:w-full transition-all duration-300"></span>
                  </a>
                </div>
              </div>

              <!-- Error Messages -->
              <transition name="error" mode="out-in">
                <div v-if="errors.length" class="bg-red-50 border-l-4 border-red-400 p-4 rounded-lg">
                  <div class="flex">
                    <div class="flex-shrink-0">
                      <svg class="h-5 w-5 text-red-400" viewBox="0 0 20 20" fill="currentColor">
                        <path fill-rule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clip-rule="evenodd" />
                      </svg>
                    </div>
                    <div class="ml-3">
                      <ul class="text-sm text-red-700">
                        <li v-for="error in errors" :key="error" class="mb-1">
                          {{ error }}
                        </li>
                      </ul>
                    </div>
                  </div>
                </div>
              </transition>

              <!-- Submit Button -->
              <div>
                <button 
                  type="submit" 
                  :disabled="isLoading"
                  class="group relative w-full flex justify-center py-4 px-4 border border-transparent text-sm font-bold rounded-xl text-white bg-gradient-to-r from-indigo-600 to-cyan-600 hover:from-indigo-700 hover:to-cyan-700 focus:outline-none focus:ring-4 focus:ring-indigo-500/50 disabled:opacity-50 disabled:cursor-not-allowed transform transition-all duration-300 hover:scale-[1.02] hover:shadow-xl active:scale-[0.98]"
                >
                  <span v-if="isLoading" class="absolute left-0 inset-y-0 flex items-center pl-3">
                    <div class="w-5 h-5 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
                  </span>
                  <span :class="{ 'ml-8': isLoading }">
                    {{ isLoading ? 'กำลังเข้าสู่ระบบ...' : translate('login-btn') }}
                  </span>
                  <span v-if="!isLoading" class="absolute right-4 inset-y-0 flex items-center">
                    <svg class="w-5 h-5 transform group-hover:translate-x-1 transition-transform duration-200" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M13 7l5 5m0 0l-5 5m5-5H6"></path>
                    </svg>
                  </span>
                </button>
              </div>
            </form>
          </div>
